
import React, { Component } from "react"
import "./index.scss";
import { connect } from "react-redux"
import { Carousel, Button } from 'antd-mobile';

@connect(
    state => {
        return {
            guide: state.getIn(["data", "data"])
        }
    }
)



class Guide extends Component {



    componentDidMount() {
        if (localStorage.visitCount) {
            localStorage.visitCount++;
            if (localStorage.visitCount > 1) {
                // console.log(this.props.history)

                this.props.history.push("/login");
            }
        } else {
            localStorage.visitCount = 1;
            this.props.history.push("/login");

        }
    }


    goToMain = (i) => {
        // alert( this.props.history.push)
        if (i === this.props.guide.length - 1) {
            this.props.history.push("/login");
        }
    }


    render() {
        const {
            guide
        } = this.props
        // console.log(this.props.guide)
        return (
            <div >
                <div className="guide" >
                    <Carousel
                        dots={false}
                    >



                        {
                            guide.map((item, i) => (
                                <div key={i}>
                                    <img className="img1" src={item} alt="" />
                                    {i = 3 && < Button
                                        onClick={() => this.goToMain(i)}
                                        className="btnn" type="primary" inline size="small" style={{ marginRight: '4px', border: "none" }}>开启剁手时代4.0</Button>}
                                </div>
                            ))
                        }
                    </Carousel>

                </div>
            </div >

        )
    }
}

export default Guide